<template>
  <v-container
    id="data-tables"
    class="fill-height align-start"
    fluid
    tag="section"
  >
    <v-container
      fluid
    >
      <ik-data-table
        ref="statisticTable"
        :model="model"
        :use-action="false"
        :use-select="false"
        icon="mdi-calculator-variant"
        :entity-name="$t('菜品统计')"
        :filter="filter"
      >
        <template v-slot:filterLeft>
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :return-value.sync="dates"
            offset-y
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="dates"
                class="ml-auto"
                hide-details
                :label="$t('日期筛选')"
                prepend-icon="mdi-calendar"
                readonly
                style="max-width: 250px;"
                single-line
                append-icon="mdi-close"
                @click:append="clear"
                v-on="on"
              />
            </template>
            <v-date-picker
              v-model="dates"
              no-title
              range
              scrollable
              locale="de"
            >
              <v-spacer/>
              <v-btn
                text
                color="primary"
                @click="menu = false"
              >
                {{ $t('Cancel') }}
              </v-btn>
              <v-btn
                text
                color="primary"
                @click="$refs.menu.save(dates)"
              >
                {{ $t('OK') }}
              </v-btn>
            </v-date-picker>
          </v-menu>
        </template>
        <template v-slot:footer>
          <v-toolbar
            color="white"
            flat
          >
            <v-spacer/>
            <v-btn
              class="mb-2 mr-2"
              color="primary"
              dark
              @click="printBySale"
            >
              {{ $t('按销量打印') }}
            </v-btn>
            <v-btn
              class="mb-2"
              color="primary"
              dark
              @click="print"
            >
              {{ $t('打印') }}
            </v-btn>
          </v-toolbar>
        </template>
      </ik-data-table>
    </v-container>
  </v-container>
</template>

<script>

  import DishAnalysis from '../../model/DishAnalysis'
  import { IkDataTable } from 'innerken-utils'
  import Utils from '../../components/Utlis/Utils'
  import { PrinterSaleBon, PrinterSaleBonBySale } from '../../components/Utlis/api'

  export default {
    components: {
      IkDataTable,
    },
    data: function () {
      return ({
        model: DishAnalysis,
        dates: [],
        menu: false,
        dishCategoryTypeIds: '',
      })
    },
    computed: {
      filter () {
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        return { start: this.dates[0], end: this.dates[1] }
      },
    },
    watch: {
      dates: function (val) {
      },
    },
    methods: {
      clear () {
        this.dates = []
      },
      printBySale () {
        if (this.$refs.statisticTable.filterItem.categoryTypeId) {
          this.dishCategoryTypeIds = this.$refs.statisticTable.filterItem.categoryTypeId
        }
        if (this.dates.length === 0) {
          Utils.showError('Bitte wählen Sie ein Startdatum und Enddatum')
        } else {
          Utils.showConfirm('Bist du sicher?', 'Möchten Sie Umsatz Bon drucken?', () => {
            PrinterSaleBonBySale(this.dishCategoryTypeIds, this.filter).then(() => {
              Utils.toast('Erfolgreich drucken!')
            })
          })
        }
      },
      print () {
        if (this.$refs.statisticTable.filterItem.categoryTypeId) {
          this.dishCategoryTypeIds = this.$refs.statisticTable.filterItem.categoryTypeId
        }
        if (this.dates.length === 0) {
          Utils.showError('Bitte wählen Sie ein Startdatum und Enddatum')
        } else {
          Utils.showConfirm('Bist du sicher?', 'Möchten Sie Umsatz Bon drucken?', () => {
            PrinterSaleBon(this.dishCategoryTypeIds, this.filter).then(() => {
              Utils.toast('Erfolgreich drucken!')
            })
          })
        }
      },
    },
  }
</script>
