import GlobalSettings from '../components/Settings/GlobalSettings'
import { hillo, IKDataEntity } from 'innerken-utils'
import Category from './Category'
import CategoryType from './CategoryType'

const entity = {
  dishId: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
  },
  code: {
    displayName: 'code',
  },
  name: {
    displayName: 'name',
  },
  categoryId: {
    displayName: 'labelName',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Category.getList,
        itemText: '_langsname',
      },
    },
  },
  categoryTypeId: {
    displayName: 'labelType',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: CategoryType.getList,
      },
    },
  },
  price: {
    displayName: 'price',
  },
  weeklyCount: {
    displayName: 'weeklyCount',
  },
  monthlyCount: {
    displayName: 'monthlyCount',
  },
  totalCount: {
    displayName: 'totalMoneyCount',
  },
}
const config = {
  load: async function (filter) {
    return (await hillo.get('BackendData.php', Object.assign({
      op: 'dishStatistic',
      lang: GlobalSettings.lang(),
    }, ...filter)))
      .content
  },
}

export default IKDataEntity.ModelFactory(entity, config)
